@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');


body{
    margin:0;
    padding: 0;
    background-color: #000000;
    font-family: 'Lato', sans-serif;
    color: #3d5cb8;
}

*{
    box-sizing: border-box;
}

ul{
    list-style: none;
}

a{
    text-decoration: none;
}

button{
    outline: none;
    border: none;
}

input{
    outline: none;
    border: none;
}

.logo img{
    width: 90px;
}

#main{
    width:100%;
    height:919px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../images/banner.png');
    z-index: 0;
}

#main::before{
    position: absolute;
    content: "";
    background: radial-gradient(ellipse at center, rgba(181, 8, 8, 0.38) 0%, rgba(0, 0, 0, 0.6) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#61b50808', endColorstr='#99000000',GradientType=1 );
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

nav{
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: fixed;
	left: 0;
	top: 0;
	width:100%;
	z-index: 1;
    background-color: transparent;
    transition: 0.6s ease-in-out;
}

nav.active{
	box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.157);
	background-color: #000000;
}

nav ul{
	display: flex;
}

.active{
    background-color: #FF1414;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
	transition: all ease 0.2s;
}

.active:hover{
    background-color: #000000;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
	transition: all ease 0.2s;
}

nav ul li a{
    font-family: 'Lato', serif;
	height:40px;
	line-height: 43px;
	margin: 3px;
	padding: 0 22px;
	display: flex;
	font-size: 0.9rem;
	text-transform: uppercase;
	font-weight: 500;
	color:#ffffff;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
}

nav ul li a:hover{
	background-color: #FF1414;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
	transition: all ease 0.2s;
}

nav .menu-btn, .menu-icon{
    display:none;
}

.header-heading{
    text-align: center;
    font-family: 'Montserrat', sans-serif;
	width: 900px;
	position: absolute;
    left: 50%;
	top: 55%;
	transform: translate(-50%,-55%);
}

.header-heading span{
    color: #FF1414;
}

.header-heading .details{
    font-size: 23px;
    color: #ffffff;
}

.header-heading h2{
    font-family: 'Lato', serif;
	font-size: 2.7rem;
	margin:0;
	letter-spacing: 2px;
	color:#ffffff;
}

.header-heading h1{
    font-family: 'Lato', serif;
	font-size: 5rem;
	margin:0;
	letter-spacing: 2px;
	color:#ffffff;
}

.header-btns{
    display: flex;
    margin-top: 40px;
    margin-left: 40%;
}

.header-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
	color:#ffffff;
}

.header-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #FF1414;
}


#features{
    width:100%;
    height:120vh;
    box-sizing: border-box;
    font-family: 'Lato', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    background-color: #000000;
}

#features h1{
    color: #fff;
    font-size: 3rem;
}

.a-container{
    display: flex;
    justify-content: center;
    align-content: center;
}

.a-box{
    background-color: #000000;
    width:250px;
    height: 400px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    transition: 0.4s ease-in-out;
    position: relative;
}

.a-b-img{
    width: 100%;
    height: 60%;
}

.a-b-img img{
    padding: 15px;
    margin-top: 40px;
    width:100%;
    height: 50%;

}

.a-box:hover{
    border: 1px solid #FF1414;
    box-shadow: 2px 2px 12px rgba(184, 0, 0, 0.445);
}

.a-b-text{
    width: 100%;
    background-color:#000000;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}

.a-b-text h2{
    color: #fff;
}


#offer{
    padding-top: 30px;
    height:1068px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../images/offer.png");
    text-align: center;
}

.offer-heading{
    text-align: center;
    font-family: 'Lato', serif;
	width: 600px;
	position: absolute;
    left: 50%;
	top: 55%;
	transform: translate(-50%,-55%);
}

.offer-heading span{
    color: #FF1414;
}

.offer-heading .details{
    font-size: 1.2rem;
    color: #ffffff;
    font-weight: bold;
}

.offer-heading h1{
    font-family: 'Lato', serif;
	font-size: 4rem;
	margin:0;
	letter-spacing: 2px;
	color:#ffffff;
}
.offer-btns{
    display: flex;
    margin-top: 40px;
    margin-left: 35%;
}
.offer-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
    color: #ffffff;
}

.offer-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #FF1414;
}

#about{
    margin-top:100px ;
	width:100%;
	height: 120vh;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 50px 5% 0 5%;
	position: relative;
}

.about-text{
    width: 45%;
    color: #fff;
    list-style: circle;
}

.about-text h1{
	font-size: 3rem;
	color:#FF1414;
    font-weight: 500;
    margin: 0;
	padding: 0;
}

.about-text p{
    width: 80%;
	font-size: 1.2rem;
	color:#cecece;
	
}

.about-text button{
    margin-top: 20px;
	width: 140px;
	height: 45px;
	border-radius: 10px;
    font-size: 1.2rem;
    font-family: 'Lato', sans-serif;
	border: none;
    outline: none;
    cursor: pointer;
    color: #ffffff;
	background-color:#FF1414;
}

.about-text button:hover{
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.61);
    background-color: #000000;
    transition: all ease 0.3s;
    color: #ffffff;
}

.about-image{
    width: 50%;
}

.about-image img{
  width: 600px;
}

#contact{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#contact h1{
    color: #fff;
    font-size: 3rem;
}

#contact form{
    width:600px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}

#contact form input, #contact form textarea{
    width:100%;
    height:50px;
    margin:5px 0;
    padding: 10px;
    border: none;
    outline: none;
    background-color: #ffffff2d;
    color: #ffffff;
    border-radius: 5px;
}

#contact form textarea{
    height: 150px;
}

#contact form input[type="submit"]{
    height: 45px;
    cursor: pointer;
    font-family: 'Lato', sans-serif;
    background: linear-gradient(90deg, #FF1414 20%,#f70000);
    color: #ffffff;
    font-size: .9rem;
    text-transform: uppercase;
}


::selection{
    background: hsl(225,50%,48%);
    color: hsl(0,0%,100%);
}


@media screen and (max-width:620px){
    #contact form{
        width:90%;
    }
}

@media screen and (max-width:1190px){
	#main{
		background-size: 1150px !important;
		
	}

	.header-heading{
		left: 50%;
		top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
    }

    .header-heading h2{
        font-size: 1.5rem;
    }

    .header-heading h1{
        font-size: 3rem;
    }

    .header-heading .details{
        font-size: 1rem;
    }

    .offer-heading{
		left: 50%;
		top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
    }

    .offer-heading h2{
        font-size: 1.5rem;
    }

    .offer-heading h1{
        font-size: 3rem;
    }

    .offer-heading .details{
        font-size: 1rem;
    }

	.about-image img{
		height:400px;
    }

    #features{
		height:auto;
    }

    .a-container{
		flex-wrap: wrap;
	}

    .a-box{
		flex-grow: 1;
	}

    .a-b-img img{
		object-fit:contain;
    }
}

@media screen and (max-width:970px){
	#main{
		height: 650px;
	}

	.header-heading{
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
    }

    .offer-heading{
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
	}

	.about-image{
        display:none;
    }

    .header-btn{
        margin-left: 40%;
        margin-top: 20px;
    }

    .offer-btn{
        margin-left: 37%;
        margin-top: 20px;
    }

	#about{
		justify-content: center;
        padding-top: 0;
        text-align: center;
        margin-top: 0;
	}

	.about-text{
        width: 90%;
	}

	.about-text h1{
		font-size:4rem;
    }

    .about-text p{
        width: 100%;
    }

    #features{
        margin-bottom: 100px;
    }
}

@media screen and (max-width:600px){
	.header-heading{
		width:60%;
    }

    .offer-heading{
		width:60%;
    }

    .header-btn{
        margin-left: 30%;
        margin-top: 20px;
    }

    .offer-btn{
        margin-left: 28%;
        margin-top: 20px;
    }
}

@media screen and (max-width: 490px) {
    .header-btn{
        margin-left: 23%;
        margin-top: 20px;
    }

    .offer-btn{
        margin-left: 23%;
        margin-top: 20px;
    }
}

@media screen and (max-width:1100px){
    .menu-icon{
        display:block;
    }

    nav{
        justify-content: space-between;
        height: 65px;
        padding: 0 30px;
    }

    .logo img{
        width:70px;
    }

    .header-btns{
        margin: 0;
    }

    .offer-btns{
        margin: 0;
    }

    .menu{
        display:none;
        position:absolute;
        top:65px;
        left:0;
        background-color:#000000;
        border-bottom:4px solid #FF1414;
        width:100%;
        padding:0;
        margin:0;
    }

    .menu li{
        width:100%;
    }

    nav .menu li a{
        width:100%;
        height:40px;
        justify-content: center;
        align-items: center;
        margin:0;
        padding: 25px;
        border:1px solid rgba(38,38,38,0.03);
    }

    nav .menu-icon{
        cursor:pointer;
        float:right;
        padding:28px 20px;
        position:relative;
        user-select: none;
    }

    nav .menu-icon .nav-icon{
        background-color:#ffffff;
        display:block;
        height:2px;
        position:relative;
        transition: background 0.2s ease-out;
        width:24px;
    }

    nav .menu-icon .nav-icon:before, nav .menu-icon .nav-icon:after{
        background:rgb(255, 255, 255);
        content:'';
        display:block;
        height:100%;
        position:absolute;
        transition:all ease-out 0.2s;
        width:100%;
    }

    nav .menu-icon .nav-icon:before{
        top:6px;
    }

    nav .menu-icon .nav-icon:after{
        top:-6px;
    }

    nav .menu-btn:checked ~ .menu-icon .nav-icon{
        background:transparent;
    }

    nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
        transform: rotate(-45deg);
        top:0;
    }

    nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
        transform: rotate(45deg);
        top:0;
    }

    nav .menu-btn{
        display:none;
    }

    nav .menu-btn:checked ~ .menu{
        display:block;
    }
}

@media screen and (max-width:400px){
    .a-box{
        width: 100%;
        height:400px;
    }
}